import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Compare } from '@microsoft/sp-client-base';
import { StringHelper } from '@ms/odsp-utilities-bundle';

import { IPropertyPaneData } from './IPropertyPane';
import { IPropertyPanePage } from '../propertyPanePage/IPropertyPanePage';
import PropertyPanePage from '../propertyPanePage/PropertyPanePage';
import PropertyPaneTitleBar from '../propertyPaneTitleBar/PropertyPaneTitleBar';
import Strings from '../loc/Strings.resx';
import propertyPaneStyles from './PropertyPane.module.scss';
import propertyPanePageStyles from '../propertyPanePage/PropertyPanePage.module.scss';
// VSO: Bug 227235 Remove this dependency on Canvas11y

import {
  Fabric,
  FocusTrapZone,
  KeyCodes,
  css
} from 'office-ui-fabric-react';

import {
  getFocusableChildren,
  getFirstFocusableChild,
  isKey,
  KeyCodes as ExtendedKeyCodes
} from '../../../appBase/components/sp-canvas/a11y/CanvasA11y';

/**
 * Propertypane component props.
 */
export interface IPropertyPaneProps {
  data: IPropertyPaneData;
}

/**
 * Propertypane component state.
 */
export interface IPropertyPaneState {
  isSaveButtonEnabled: boolean;
  saveConfirmationMessage: string;
  currentPage: number;
}

/**
 * A constant value to delay the clearing of save confirmation message.
 */
const CLEAR_DELAY: number = 100;

/**
 * A constant value to set when there is no web part to configure.
 */
const NO_WEBPART_ID: string = 'No_Web_Part_ID';

/**
 * Page direction.
 */
enum PageDirection {
  back = -1,
  next = 1
}

/**
 * Propertypane component.
 */
export default class PropertyPane extends React.Component<IPropertyPaneProps, IPropertyPaneState> {

  public refs: {
    [key: string]: Element,
    propertyPane: HTMLElement
  };

  private _properties: IPropertyPaneProps;
  private _descriptionId: string;
  private _invalidEntries: { [key: string]: boolean };
  private _cssClassForActionableElement: string;
  private _propertyPane: HTMLElement;
  private _propertyPaneContent: HTMLElement;
  private _currentlyConfiguredWebPartId: string;
  private _isPropertyPaneReplaced: boolean;

  /**
   * Constructor to instantiate the propertypane component.
   */
  constructor(props: IPropertyPaneProps) {
    super(props);

    this.state = {
      isSaveButtonEnabled: false,
      saveConfirmationMessage: '',
      currentPage: 1
    };

    this._currentlyConfiguredWebPartId = props.data.webPartId;

    // method bindings.
    this._onChange = this._onChange.bind(this);
    this._onPropertyPaneKeyDown = this._onPropertyPaneKeyDown.bind(this);
    this._handleSaveClick = this._handleSaveClick.bind(this);
    this._handleCloseClick = this._handleCloseClick.bind(this);
    this._setContainerDiv = this._setContainerDiv.bind(this);

    this._invalidEntries = {};
  }

  /**
   * React component lifecycle event.
   * @param props - new props for the component.
   */
  public componentWillReceiveProps(props: IPropertyPaneProps): void {
    if (this._currentlyConfiguredWebPartId !== props.data.webPartId || props.data.shouldResetState) {
      const webPartId: string = props.data.webPartId ? props.data.webPartId : NO_WEBPART_ID;
      this._resetState(webPartId);
    } else {
      this.setState({
        saveConfirmationMessage: ''
      } as IPropertyPaneState);
    }
  }

  public componentDidMount(): void {
    // @todo: for some god forsaken reason the z-index in the class does not apply. Hence this hack
    // Some react thing that I don't have time to dig into right now. Perhaps later.
    const domNode: HTMLElement = ReactDOM.findDOMNode(this) as HTMLElement;
    domNode.style.zIndex = '999';

    if (this.props.data.onRendered) {
      this.props.data.onRendered();
    }
    // This is the hack to workaround a react bug that doesn't set the focus in componentDidMount properly
    // VSO:Bug 215692 Remove the hack and implement focus methods in fabric react
    this._setTimeOutForFocus();
  }

  public componentDidUpdate(props: IPropertyPaneProps): void {
    // When empty property pane is loaded, onRendered method will be undefined.
    if (this.props.data.onRendered) {
      this.props.data.onRendered();
      if (this._isPropertyPaneReplaced) {
        this._updateTabIndexes(0, true);
        this._setTimeOutForFocus();
        this._isPropertyPaneReplaced = false;
      }
    }
  }

  /**
   * React component lifecycle method.
   * Added this method to avoid, unnecessary renders.
   */
  public shouldComponentUpdate(nextProps: IPropertyPaneProps, nextState: IPropertyPaneState): boolean {
    return !Compare.shallowCompare(this.props, nextProps) || !Compare.shallowCompare(this.state, nextState);
  }

  public render(): React.ReactElement<{}> {
    this._properties = this.props.data.properties;

    return (
      <Fabric>
        <div role='alert'
          aria-live='polite'
          className={ propertyPaneStyles.propertyPaneScreenReaderOnly }>
          <span>{ this.state.saveConfirmationMessage }</span>
        </div>
        <FocusTrapZone
          isClickableOutsideFocusTrap='true'
          firstFocusableSelector={ this._cssClassForActionableElement }
          forceFocusInsideTrap={ false }>
          <div
            className={ css('ms-font-m', propertyPaneStyles.propertyPane) }
            role='dialog'
            aria-labelledby='propertyPaneTitleId'
            aria-describedby={ this._descriptionId }
            ref={ (c: HTMLElement): HTMLElement => this._propertyPane = c }
            onKeyDown={ this._onPropertyPaneKeyDown }>
            <PropertyPaneTitleBar title={ this.props.data.title } onClose={ this._handleCloseClick } />
            { this._renderContent() }
          </div>
        </FocusTrapZone>
      </Fabric>
    );
  }

  /**
   * Resetting the PropertyPane state.
   * @param webPartId - web part id to be configured with.
   */
  private _resetState(webPartId: string): void {
    this._isPropertyPaneReplaced = true;
    this._currentlyConfiguredWebPartId = webPartId;

    this.setState({
      isSaveButtonEnabled: false,
      saveConfirmationMessage: '',
      currentPage: 1
    });
  }

  /**
   * Renders the content on the property pane, below the title.
   */
  private _renderContent(): React.ReactElement<{}> {
    if (this._shouldRenderEmptyPage) {
      return this._renderNoConfigurationState();
    } else {
      return (
        <div>
          { this._renderPage() }
          <div className={ propertyPanePageStyles.propertyPaneFooter }>
            { this._renderApplyButton() }
            { this._pageNavigationButtons() }
          </div>
        </div>
      );
    }
  }

  /**
   * Returns if the empty page should be rendered in property pane.
   */
  private get _shouldRenderEmptyPage(): boolean {
    return (!this.props.data.settings ||
      !this.props.data.settings.pages ||
      this.props.data.settings.pages.length === 0 ||
      !this.props.data.settings.pages[this.state.currentPage - 1].groups ||
      this.props.data.settings.pages[this.state.currentPage - 1].groups.length === 0);
  }

  /**
   * Renders the Apply button, if property pane is not reactive.
   */
  private _renderApplyButton(): React.ReactElement<{}> {
    if (!this.props.data.isReactive) {
      return (
        <div className={ propertyPanePageStyles.propertyPaneButtonCommands }>
          <button className='ms-Button ms-Button--primary' disabled={ !this.state.isSaveButtonEnabled }
            onClick={ this._handleSaveClick }>
            <span className='ms-Button-label'>{ Strings.Save }</span>
          </button>
        </div>
      );
    }
  }

  /**
   * Renders page navigation buttons.
   */
  private _pageNavigationButtons(): React.ReactElement<{}> {
    if (this.props.data.settings.pages.length > 1) {
      return (
        <div className={ propertyPanePageStyles.propertyPanePageNav }>
          <button
            className={ propertyPanePageStyles.propertyPaneBackNavLink }
            disabled={ this.state.currentPage === 1 }
            onClick={ () => { this._flipPage(PageDirection.back); } }>
            <i className={ css(
              this._arrowKeyIcon(PageDirection.back),
              propertyPanePageStyles.propertyPaneBackArrow) }></i>
            { Strings.PropertyPaneBackPage }
          </button>
          <span role='status' className={ css(propertyPanePageStyles.propertyPanePageOfPages) }>
            { StringHelper.format(
              Strings.PropertyPanePageOfPages,
              this.state.currentPage,
              this.props.data.settings.pages.length
            ) }
          </span>
          <button
            className={ propertyPanePageStyles.propertyPaneNextNavLink }
            disabled={ this.state.currentPage === this.props.data.settings.pages.length }
            onClick={ () => { this._flipPage(PageDirection.next); } }>
            { Strings.PropertyPaneNextPage }
            <i className={ css(
              this._arrowKeyIcon(PageDirection.next),
              propertyPanePageStyles.propertyPaneNextArrow) }></i>
          </button>
        </div>
      );
    } else {
      /* tslint:disable:no-null-keyword */
      return null;
      /* tslint:enable:no-null-keyword */
    }
  }

  /**
   * Flips the page based on the direction.
   * @param direction - direction of the page flip. Positive value is forward direction, negative is backwards.
   */
  private _flipPage(direction: PageDirection): void {
    const page: number = this.state.currentPage + direction;

    this.setState({
      currentPage: page
    } as IPropertyPaneState);

    // trigger rendering the page
    this.props.data.render();
    this._setTimeOutForFocus();
  }

  /**
   * Renders page on the property pane.
   */
  private _renderPage(): React.ReactElement<IPropertyPaneProps> {
    const pages: IPropertyPanePage[] = this.props.data.settings.pages;
    const page: IPropertyPanePage = pages[this.state.currentPage - 1];

    if (page.header && page.header.description) {
      this._descriptionId = 'propertyPaneDescriptionId';
    }

    return (
      <PropertyPanePage
        pageDescription={ page.header ? page.header.description : undefined }
        displayGroupsAsAccordion={ page.displayGroupsAsAccordion }
        onChange={ this._onChange.bind(this) }
        propertyGroups={ page.groups }
        properties={ this._properties }
        setEntryState={ (targetProperty: string, isValidEntry: boolean): void => {
          this._setEntryState(targetProperty, isValidEntry);
        } }
        setContainerDiv= { this._setContainerDiv }
        />
    );
  }

  private _setContainerDiv(containerDiv: HTMLDivElement): void {
    this._propertyPaneContent = containerDiv;
  }

  /**
   * Renders the description for no configuration scenario.
   */
  private _renderNoConfigurationState(): React.ReactElement<{}> {
    return (
      <div>
        <div className={ propertyPanePageStyles.propertyPanePageContent }>
          <div role='alert'
            aria-live='polite'
            className={ propertyPaneStyles.propertyPaneDescription }>
            { Strings.PropertyPaneEmptyStateDescription }</div>
        </div>
      </div>
    );
  }

  private _setEntryState(targetProperty: string, isValidEntry: boolean): void {

    // maintain the list of invalid entries
    if (isValidEntry) {
      if (this._invalidEntries.hasOwnProperty(targetProperty)) {
        delete this._invalidEntries[targetProperty];
      }
    } else {
      this._invalidEntries[targetProperty] = true;
    }

    // enable the save button in Reactive mode if there are no invalid entries
    if (!this.props.data.isReactive) {
      this._enableSaveButton(Object.keys(this._invalidEntries).length === 0);
    }
  }

  private _enableSaveButton(enabled: boolean): void {
    this.setState({
      isSaveButtonEnabled: enabled
    } as IPropertyPaneState);
  }

  private _onChange(targetProperty: string, value: boolean | string): void {
    this._properties[targetProperty] = value;

    if (!this._invalidEntries.hasOwnProperty(targetProperty)) {
      this._raiseEvent(targetProperty, value);
    }
  }

  private _handleSaveClick(): void {
    this.setState({
      saveConfirmationMessage: Strings.SaveConfirmation
    } as IPropertyPaneState);

    this.props.data.onSave();
    this._enableSaveButton(false);

    setTimeout(() => {
      this.setState({
        saveConfirmationMessage: ''
      } as IPropertyPaneState);
    }, CLEAR_DELAY);
  }

  private _handleCloseClick(): void {
    this._resetState(NO_WEBPART_ID);
    this._updateTabIndexes(-1, false);

    this.props.data.onClose();
  }

  /* tslint:disable:no-any */
  private _raiseEvent(targetProperty: string, value: any): void {
    /* tslint:enable:no-any */
    this.props.data.onPropertyChange(targetProperty, value);
  }

  private _onPropertyPaneKeyDown(ev: React.KeyboardEvent): void {
    if (isKey(KeyCodes.escape, ev)) {
      ev.preventDefault();
      this._handleCloseClick();
    } else if (isKey(ExtendedKeyCodes.P, ev, true)) {
      ev.preventDefault();
      this.props.data.onLostFocus();
    }
  }

  private _arrowKeyIcon(dir: PageDirection): string {
    const rtl: boolean = document.dir === 'rtl';
    const iconCSS: string = 'ms-Icon ms-Icon--Chevron';

    if (dir === PageDirection.back) {
      return iconCSS + (rtl ? 'Right' : 'Left');
    } else {
      return iconCSS + (rtl ? 'Left' : 'Right');
    }
  }

  private _setTimeOutForFocus(): void {
    setTimeout(() => {
      const propertyPane: HTMLElement = this._shouldRenderEmptyPage ?
        this._propertyPane : this._propertyPaneContent;
      this._focusOnFirstActionableElement(propertyPane);
    }, 0);
  }

  private _focusOnFirstActionableElement(propertyPane: HTMLElement): void {
    if (propertyPane) {
      const firstElement: HTMLElement = getFirstFocusableChild(propertyPane);
      if (firstElement) {
        firstElement.focus();
      }
    }
  }

  /**
   * Method to update the tab indexes of all the elements in the root to the given value.
   * @param tabIndex
   */
  private _updateTabIndexes(tabIndex: number, ignoreTabIndexes: boolean): void {
    const rootElement: HTMLElement = this._propertyPane as HTMLElement;
    const elements: HTMLElement[] = getFocusableChildren(rootElement, ignoreTabIndexes);

    for (const el of elements) {
      (el as HTMLElement).tabIndex = tabIndex;
    }
  }
}



